function draw(ctx, animationFrameId, width, height, tmax, reuse, condition) {
    const imgData = reuse ? ctx.getImageData(0, 0, ctx.canvas.width, ctx.canvas.height) : ctx.createImageData(ctx.canvas.width, ctx.canvas.height);
    var pixels = imgData.data;

    for (var x, y, t, r, g, b, a, xc = 0, yc = 0, i = 0; i < pixels.length; i += 4, xc++) {
        if (xc === width) {
            xc = 0;
            yc++;
        }
        x = xc + 1 - Math.floor(width / 2);
        y = Math.floor(height / 2) - yc;

        const k = animationFrameId % (2 * tmax);
        if (k < tmax) t = k;
        else t = 2 * tmax - k - 1;

        r = pixels[i];
        g = pixels[i + 1];
        b = pixels[i + 2];
        a = pixels[i + 3];

        if (condition.graph(x, y, t, r, g, b, a)) {
            pixels[i] = condition.red(x, y, t, r, g, b, a);
            pixels[i + 1] = condition.green(x, y, t, r, g, b, a);
            pixels[i + 2] = condition.blue(x, y, t, r, g, b, a);
        } 
        pixels[i + 3] = condition.alpha(x, y, t, r, g, b, a);
    }

    ctx.putImageData(imgData, 0, 0);
}

export default draw;
