import { Link } from 'react-router-dom';
import photosData from './photosData';

function Home() {
    const photosComponents = photosData.map((id) => {
        return (
            <div
                key={id}
                className='photo-preview-container'>
                <Link to={'/' + id}>
                    <img
                        className='photo-preview'
                        src={process.env.PUBLIC_URL + '/photos/' + id}
                        alt='oops' />
                </Link>
            </div>
        )
    });

    return (
        <div>
            <h1 className='title'> Paintings Repository </h1>
            <Link to={'/animation'}>
                <div className='photo-preview-container'>
                    <img className='photo-preview'
                     src={process.env.PUBLIC_URL + 'blank.png'}
                     alt='oops' />

                </div>
            </Link>
            {photosComponents}
        </div>
    );
}

export default Home;
