import React from 'react';
import InputGraph from './InputGraph';
import InputColor from './InputColor';

function Form(props) {
    const { tmax, reuse, equation, validity, component, handleChange, handleSubmit } = props;
    const inputColor = ['red', 'green', 'blue', 'alpha'].map((item) => {
        return <InputColor
            key={item}
            id={item}
            equation={equation[item]}
            inputValid={validity[item]}
            component={component[item]}
            handleChange={handleChange}
            handleSubmit={handleSubmit} />
    });

    return (
        <div className='form'>
            <InputGraph
                tmax={tmax}
                reuse={reuse}
                equation={equation.graph}
                inputValid={validity.graph}
                component={component.graph}
                handleChange={handleChange}
                handleSubmit={handleSubmit} />
            {inputColor}
        </div>
    );
}

export default Form;
