import React from 'react';

function InputGraph(props) {
    const { tmax, reuse, equation, inputValid, component, handleChange, handleSubmit } = props;

    return (
        <div className='graph'>
            <div className='row'>
                <label className='graph-title'> graph </label><br />
                <div className='column'>
                    <label> Equation </label><br />
                    <textarea name='equation' id='graph' value={equation} onChange={handleChange} /><br />
                    <label> Operation </label>
                    <select name='operation' id='graph' onChange={handleChange}>
                        <option></option>
                        <option value='&&'> AND </option>
                        <option value='||'> OR </option>
                    </select>
                    <button id='graph' onClick={handleSubmit}> Add </button><br />
                    {!inputValid && <span className='error-message'>invalid syntax</span>}
                    <br /><label> T Max </label><br />
                    <input type='number' name='tmax' value={tmax} onChange={handleChange} /><br />
                    <input type='checkbox' name='reuse' checked={reuse} onChange={handleChange} />
                    <label> Reuse Canvas </label>
                </div>
                <div className='column'>
                    {component}
                </div>
            </div>
        </div>

    );
}

export default InputGraph;
