/* eslint-disable no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-new-func */
import React, { useState, useCallback, useReducer } from 'react';
import Animation from './Canvas';
import Form from './Form';

function App() {
  const width = Math.floor(Math.min(0.9*window.innerWidth, 0.9*window.innerHeight));
  const height = width;
  const [tmax, setTmax] = useState(200);
  const [reuse, setReuse] = useState(true);
  const [operation, setOperation] = useState({
    graph: null,
    red: null,
    green: null,
    blue: null,
    alpha: null
  });
  const [equation, setEquation] = useState({
    graph: '',
    red: '',
    green: '',
    blue: '',
    alpha: ''
  });
  const [input, updateInput] = useReducer(
    (state, action) => {
      const { name, id, value } = action;
      var stateNew = [];
      if (name === 'equation') {
        state[id].forEach((item) => { stateNew.push(item) });
        stateNew.push({ op: operation[id], eq: equation[id] });
        if (stateNew.length === 1) stateNew[0].op = null;
      } else if (name === 'remove') {
        state[id].forEach((item) => { if (item.eq !== value) stateNew.push(item) });
        if (stateNew[0] && stateNew[0].op) stateNew[0].op = null;
      }
      return { ...state, [id]: stateNew };
    },
    {
      graph: [
        {
          op: null,
          eq: 'x*x + y*y < t*t'
        },
        {
          op: '&&',
          eq: 'Math.cos(t*x)>Math.sin(y)'
        },
        {
          op: '||',
          eq: 'Math.cos(t*y)>Math.sin(y)'
        }
      ],
      red: [
        {
          op: null,
          eq: '(3 * t) % 256'
        },
        {
          op: '-',
          eq: '(Math.cos(t*y)>Math.sin(y) ? -100 : 0)'
        }
      ],
      green: [
        {
          op: null,
          eq: '(5 * t) % 256'
        }
      ],
      blue: [
        {
          op: null,
          eq: '(2 * t) % 256'
        },
        {
          op: '+',
          eq: '(x*x + y*y < t*t ? -100 : 0)'
        }
      ],
      alpha: [
        {
          op: null,
          eq: '255'
        }
      ]
    }
  );
  const [validity, setValidity] = useState({
    graph: true,
    red: true,
    green: true,
    blue: true,
    alpha: true
  });
  const condition = {
    graph: useCallback(
      new Function('x', 'y', 't', 'r', 'g', 'b', 'a', 'return ' + inputToString('graph')),
      [input.graph]
    ),
    red: useCallback(
      new Function('x', 'y', 't', 'r', 'g', 'b', 'a', 'return ' + inputToString('red')),
      [input.red]
    ),
    green: useCallback(
      new Function('x', 'y', 't', 'r', 'g', 'b', 'a', 'return ' + inputToString('green')),
      [input.green]
    ),
    blue: useCallback(
      new Function('x', 'y', 't', 'r', 'g', 'b', 'a', 'return ' + inputToString('blue')),
      [input.blue]
    ),
    alpha: useCallback(
      new Function('x', 'y', 't', 'r', 'g', 'b', 'a', 'return ' + inputToString('alpha')),
      [input.alpha]
    )
  };
  const component = {
    graph: useCallback(
      generateComponent('graph'),
      [input.graph]
    ),
    red: useCallback(
      generateComponent('red'),
      [input.red]
    ),
    green: useCallback(
      generateComponent('green'),
      [input.green]
    ),
    blue: useCallback(
      generateComponent('blue'),
      [input.blue]
    ),
    alpha: useCallback(
      generateComponent('alpha'),
      [input.alpha]
    )
  };

  function inputToString(id) {
    var inputString = '';
    input[id].forEach((item) => {
      if (item.op)
        inputString += item.op;
      inputString += item.eq;
    });
    return inputString;
  }
  function generateComponent(id) {
    return input[id].map((item) => {
      var inputString = '';
      if (item.op)
        inputString += item.op === '&&' ? 'AND ' : item.op === '||' ? 'OR ' : item.op + ' ';
      inputString += item.eq;
      return (
        <div key={item.eq}>
          <button
            name='remove'
            id={id}
            value={item.eq}
            onClick={handleChange}> X </button>
          <span> {inputString} </span>
        </div>
      );
    });
  }
  function evaluateValidity(id) {
    const op = operation[id];
    const eq = equation[id];
    // evaluates that opInput exists
    // evaluates that eqInput is a valid inequality
    if ((!op && input[id].length > 0) || eq.trim().length === 0)
      return false;
    else
      for (var i = 0; i < input[id].length; i++)
        if (input[id][i].eq === eq) return false;
    return true;
  }
  function handleChange(event) {
    const { name, id, value, checked } = event.target;
    if (name === 'tmax') setTmax(value);
    else if (name === 'reuse') setReuse(checked);
    else if (name === 'operation') setOperation({ ...operation, [id]: value });
    else if (name === 'equation') setEquation({ ...equation, [id]: value });
    else updateInput({ name: 'remove', id: id, value: value });
  }
  function handleSubmit(event) {
    event.preventDefault();
    const { id } = event.target;
    console.log(id);
    const inputValidity = evaluateValidity(id);
    setValidity({ ...validity, [id]: inputValidity });
    if (inputValidity) updateInput({ name: 'equation', id: id });
  }

  return (
    <div>
      <h1 className="title"> Animations </h1>
      <Animation
        width={width}
        height={height}
        tmax={tmax}
        reuse={reuse}
        condition={condition} />
      <br />
      <Form
        tmax={tmax}
        reuse={reuse}
        equation={equation}
        validity={validity}
        component={component}
        handleChange={handleChange}
        handleSubmit={handleSubmit} /> 
    </div>
  );
}

export default App;

