import { Link, useParams } from 'react-router-dom';

function Painting() {
    const { id } = useParams();
    var pos = id.indexOf("___");
    const artist = id.substring(0, pos).replaceAll("_", " ");
    const artwork = id.substring(pos + 3, id.length - 4).replaceAll("_", " ");
    return (
        <div>
            <br />
            <Link to='/'>
                <button> {'<'} </button>
            </Link>
            <div className='painting-title'>
                <span className='artwork'>{artwork}</span><br />
                <span className='artist'>{artist}</span><br /><br />
            </div>
            <img
                className='photo-preview'
                src={process.env.PUBLIC_URL + '/photos/' + id}
                alt='oops' />
        </div>
    );
}

export default Painting;
