import React from 'react';

function InputColor(props) {
    const { id, equation, inputValid, component, handleChange, handleSubmit } = props;
    return (
        <div className={id}>
            <div className='row'>
                <label className={id + '-title'}> {id} </label><br />
                <div className='column'>
                    <label> Equation </label><br />
                    <textarea name='equation' id={id} value={equation} onChange={handleChange} /><br />
                    <label> Operation </label>
                    <select name='operation' id={id} onChange={handleChange}>
                        <option></option>
                        <option value='+'> + </option>
                        <option value='-'> - </option>
                        <option value='*'> * </option>
                        <option value='/'> / </option>
                        <option value='%'> % </option>
                    </select>
                    <button id={id} onClick={handleSubmit}> Add </button><br />
                    {!inputValid ? <span className='error-message'>invalid syntax</span> : <br />}
                </div>
                <div className='column'>
                    {component}
                </div>
            </div>
        </div>

    );
}

export default InputColor;
